import { Link } from "gatsby"
import * as React from "react"
import { FaInstagram } from '@react-icons/all-files/fa/FaInstagram';
import { SiUnsplash } from '@react-icons/all-files/si/SiUnsplash';
import { FaRegEnvelope } from '@react-icons/all-files/fa/FaRegEnvelope';
import { Helmet } from "react-helmet";

export default function Header(props) {
    const [isActive, setisActive] = React.useState(false);

    return (
        <header>
            <Helmet>
                <meta charSet="utf-8" />
                <title>conti.photos • {props.headerTitle}</title>
                <meta name="description" content={props.headerDescription} />
            </Helmet>
            <nav className="navbar">
                <div className="container">
                    <div className="navbar-brand">
                        <Link className="navbar-item" to="/">
                            <h2 className="title is-size-3">conti.photos</h2>
                        </Link>
                        <a role="button" className={`navbar-burger ${isActive ? "is-active" : ""}`} aria-label="menu" aria-expanded="false" onClick={() => { setisActive(!isActive); }}>
                            <span aria-hidden="true"></span>
                            <span aria-hidden="true"></span>
                            <span aria-hidden="true"></span>
                        </a>
                    </div>
                    <div className="navbar-menu" className={`navbar-menu ${isActive ? "is-active" : ""}`}>
                        <div className="navbar-start">
                            <Link to="/about/" className="navbar-item" activeClassName="active">about</Link>
                            <Link to="/collections/" className="navbar-item" activeClassName="active">collections</Link>
                        </div>
                    </div>
                    <div className="navbar-end">
                        <div className="navbar-item">
                            <div className="buttons">
                                <a href="https://www.instagram.com/fabricont/" className="navbar-item is-size-5"><FaInstagram /></a>
                                <a href="https://unsplash.com/@conti_photos" className="navbar-item is-size-5"><SiUnsplash /></a>
                                <a href="mailto:info@conti.photos" className="navbar-item is-size-5"><FaRegEnvelope /></a>
                            </div>
                        </div>
                    </div>
                </div>
            </nav>
        </header>
    )
}